.field {
  width: 100vw;
  height: 100vh;
  max-width: 100%;
  background-image: url("../../assets/img/board.jpg");
  display: flex;
  justify-content: center;
  align-items: center;
}

.player {
  position:absolute;
}

.player-container {
  position: absolute;
  display: flex;
  flex-direction: column;
  top: 0;
  right: 0;
}

.card-stack {
  position: relative;
  img {
    position: absolute;
    width: 120px;
  }
}

.played-card {
  right: 25px;
  transform: scale(1.3) translateX(140px) translateY(-180px) rotate(-10deg);
}

.pick-card {
  transition: all 125ms ease-in-out;
  animation: pickCardAnimation 1s ease-in-out;
  transform: scale(1.3) translateX(140px) translateY(-180px) rotate(-10deg);
}

@keyframes pickCardAnimation {
  0% {
    transform: scale(1);
  }
  40% {
    transform: scale(1.7);
  }
  60% {
    transform: scale(1.7);
  }
  100% {
    transform: scale(1.3) translateX(140px) translateY(-180px) rotate(-10deg);
  }
}

.btn-add {
  position: absolute;
  right: 32px;
  bottom: 32px;
}

.pin-bottom {
  position: absolute;
  bottom: 32px;
  left: 100px;
  right: 100px;
  padding: 10px;
}